import { isRSAA, CALL_API } from 'redux-api-middleware';

//       ,    Authorization   
//   
export const headerMiddleware = token => store => next => (action) => {
    if (isRSAA(action)){
        action[CALL_API]['headers'] = { 'Authorization': `token ${token}` };
    }
    return next(action);
};


